import { useContext, useEffect } from 'react';
import './Navbar.component.scss';
import { AppContext } from '../../AppContext';

export default function NavbarBehaviour() {
  const { isLG, isXL } = useContext(AppContext);
  const { themeColorObj } = useContext(AppContext);
  const themeColor = themeColorObj[0];

  useEffect(() => {
    const navbar = document.getElementById('navbar');
    const app = document.getElementById('app');
    let lastScrollTop = 0;

    app.addEventListener(
      'scroll',
      () => {
        const position = window.pageYOffset || app.scrollTop;
        console.log(position);
        if (position > lastScrollTop) {
          navbar.classList.remove('scrolled-up');
          navbar.classList.add('scrolled-down');
        } else if (position < lastScrollTop) {
          navbar.classList.remove('scrolled-down');
          navbar.classList.add('scrolled-up');
        }

        lastScrollTop = position <= 0 ? 0 : position;

        if (position > 300 && themeColor === 'light') {
          navbar.classList.remove('bg-gradient-dark');
          navbar.classList.add('bg-gradient-light');
          if (isLG) {
            navbar.style.padding = '16px';
          }
        } else if (position > 300 && themeColor === 'dark') {
          navbar.classList.remove('bg-gradient-light');
          navbar.classList.add('bg-gradient-dark');
          if (isLG) {
            navbar.style.padding = '16px';
          }
        } else if (position < 300) {
          navbar.classList.remove('bg-gradient-dark');
          navbar.classList.remove('bg-gradient-light');
          navbar.classList.remove('scrolled-down');
          navbar.classList.add('scrolled-up');
          navbar.style.backgroundColor = 'transparent';
          if (isXL) {
            navbar.style.padding = '32px 64px';
          }
        }
      },
      { passive: true },
    );
  });
  return;
}
